body {
    font-family: "roboto", Helvetica, sans-serif;
    width: 100%;
    font-size: 14px;
}

header .container-fluid {
    width: 100%;
}

header .container {
    max-width: 1920px;
    height: 120px;
    margin: 0 auto;
    background-color: rgb(34, 177, 76);
}

header .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_logo {
    margin-left: 15px;
}

 .menu {
    width: 600px;
    margin-right: 15px;
    display: flex;
    justify-content: space-between;
}

.header-text {
    font-size: 1.5em;
    text-decoration: none;
    color: #ffffff;
    font-weight: 400;
}

.header-text:hover,
.header-text--active
{
    color: #ffe600;
    padding-bottom: 2px;
    font-weight: 700;
}

.world_map {
    display: flex;
    width: 115.1px;
    justify-content: center;
}

.flags_of_the_countries {
    display: flex;
    width: 150px;
    justify-content: center;
}

.parts_of_the_world {
    display: flex;
    width: 120.75px;
    margin-right: 25px;
    justify-content: center;
}
