body {
    font-family: "roboto", Helvetica, sans-serif;
    width: 100%;
    font-size: 14px;
}

.container-flags {
    background-color: rgb(234, 248, 232);
}

.part {
    display: flex;
    justify-content: left;
    align-items: flex-start;
}

.part .menu1 {
    width: 150px;
    margin: 30px 0 30px 10px;
}

.part .menu1 .part-text {
    display: block;
    text-decoration: none;
    color: rgb(34, 177, 76);
    font-size: 1.2em;
    margin-bottom: 7px;
    padding: 5px 18px;
    cursor: pointer;
}

.part .menu1 .part-text:hover, .part .menu1 .part-text.active {
    color: #ffffff;
    font-weight: bold;
    background-color: rgb(34, 177, 76);
    padding: 5px 18px;
    border-radius: 17px;
}

.countryName {
    display: block;
    /* margin: 0px 50px; */
    text-align: center;
    font-size: 1.5em;
}

.countryName h1 {
    margin-top: 25px;
    margin-bottom: 0px;

}

.countries {
    display: flex;
    margin: 0px 25px 10px 25px;
    flex-wrap: wrap;
}

.country {
    display: inline-block;
    align-items: center;
    margin: 25px;
}

.flags {
    height: 273px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.flags img {
    width: 70%; 
}

.country h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.8em;
    margin: 0 auto;
    text-align: center;
    max-width: 310px;
    height: 65px;
}

.count {
    display: flex;
    align-items: center;
    margin-top: 30px;
    margin-left: 78px;
    gap: 10px;
    padding-top: 5px;
}

.list {
    width: 100%;
}

.list .count p, .sub_count {
    display: flex;
    margin: 0;
    font-size: 1.2rem;
    line-height: 14px;
    color: rgb(0, 0, 0);
    font-weight: 400;
    text-decoration: none;
    cursor: pointer;
}

.list .count .sub_count:hover,
.list .count .sub_count.active {
    color: rgb(34, 177, 76);
}

.pagination {
    margin-top: 20px;
    margin-bottom: 5px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination > nav {
    display: flex;
    align-items: center;
    gap: 16px;
}

.pag-number {
    font-size: 1.6rem;
    line-height: 14px;
    color: black;
    transition: 0.3s all ease;
    text-decoration: none;
    cursor: pointer;
}

.pag-number.active,
.pag-number:hover,
.pag-number:hover:not(.disabled) svg path {
    color: rgb(34, 177, 76);
    fill: rgb(34, 177, 76);
}


.pag-number:last-of-type svg {
    transform: rotate(-180deg);
}

.pag-number svg {
    width: 24px;
    height: 34px;
}

.pag-number.disabled {
    cursor: default;
}

.pag-number.disabled svg path {
    fill: silver;
}

