body {
    font-family: "roboto", Helvetica, sans-serif;
    width: 100%;
    font-size: 14px;
}

footer .container-fluid {
    width: 100%;
}

footer .container {
    max-width: 1920px;
    height: 280px;
    margin: 0 auto;
    background-color: rgb(34, 177, 76);
}

.socials {
    display: flex;
    margin: 0 30px;
}

.social {
    display: flex;
    margin: 0 15px;
}

footer .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.etc-social {
    display: block;
}

.social-title {
    display: flex;
    justify-content: center;
    margin: 15px auto;
    font-weight: 500;
    font-size: 2em;
    color: white;
}

.full-footer {
    display: flex;
}


.credits {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 1.8em;
    color: white;
    margin-top: 35px;
}

.etc {
    display: block;
    width: 250px;
    margin: 38px 0 20px 10px;
}

.etc .footer-text {
    display: block;
    text-decoration: none;
    color: white;
    font-size: 1.4em;
    margin-bottom: 7px;
    padding: 5px 18px;
}

.etc .footer-text:hover, .etc .footer-text--active {
    color: #ffe600;
    font-weight: bold;
}

.logo_footer {
    width: 70%;
    margin-left: 30px;
}