body {
    font-family: "roboto", Helvetica, sans-serif;
    width: 100%;
    font-size: 14px;
}

main {
    background-color: rgb(198, 255, 190);
    display: flex;
    justify-content: center;
    align-items: center;
}

.add-title {
    display: flex;
    justify-content: space-between;
    font-size: 48px;
    font-weight: bold;
    color: rgb(34, 177, 76);
}

.add-title p {
    margin: 30px auto;
}

.photo__back {
    width: 120px;
    height: 118px;
    margin-right: 30px;
    display: flex;
    justify-content: end;
    align-items: center;
    cursor: pointer;
}

.photo__back > a {
    width: 100px;
    height: 50px;
}

.img__back {
    width: 100px;
    height: 50px;
}

.photo__forward {
    width: 150px;
    height: 118px;
}

.photos {
    display: flex;
    flex-wrap: wrap;
    margin: 30px 30px;
    justify-content: space-around;
}

.photo {
    width: 350px;
    height: 250px;
}

.figure {
    display: flex;
    justify-content: center;
}

.photo__img {
    box-sizing: border-box;
    width: 300px;
    height: 200px;
    border: 4px solid rgb(198, 255, 190);
}

.photo__img:hover {
    border: 4px solid rgb(34, 177, 76);
    transition: 0.3s all ease;
}

.photo__name {
    display: flex;
    justify-content: center;
    font-size: 24px;
    font-weight: bold;
    color: rgb(34, 177, 76);
    padding-top: 8px;
    text-decoration: none;
}

.photo__name span {
    text-decoration: none;
}

.photo__main {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.arrow {
    height: 100px;
    width: 100px;
    margin: 0 20px;
    cursor: pointer;
}

.main__img {
    display: flex;
    height: 400px;
    width: 600px;
    cursor: pointer;
}


.photo__bottom {
    display: flex;
    justify-content: center;
    margin: 50px 30px 30px 30px;
}

.bottom__img {
    border: 3px solid rgb(198, 255, 190);
    width: 110px;
    height: 100%;
    margin: 5px;
    cursor: pointer;
}

.bottom__img.active {
    border: 3px solid rgb(34, 177, 76);
}

.title {
    display: flex;
    justify-content: center;
    width: 500px;
}